.section_header_home {
  background: url(../../assets/home/headerback.png) no-repeat;
  background-size: cover;
}

.wrapper_header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0px;
}

.wrapper_header ul {
  display: flex;
  align-items: center;
  gap: 40px !important;
}

.wrapper_header ul li a {
  color: #fff;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.wrapper_header ul li .header_phonenumber_box {
  display: flex;
  align-items: center;
  gap: 8px;
}

.header_phonenumber_box ion-icon {
  width: 30px;
  height: 30px;
  color: white;
}

.header_phonenumber_box .numbers {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.wrapper_header ul li .header_btn {
  padding: 16px 24px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  border-radius: 4px;
  cursor: pointer;
  border: 1px solid white;
}

.wrapper_header ul li .header_makecall_btn {
  background-color: white;
  color: var(--1, #22adaa);
}

.wrapper_header ul li .header_emailus_btn {
  background-color: transparent;
  color: var(--1, white);
}

.header_open_menu {
  display: none;
}

.header_open_menu ion-icon {
  font-size: 30px;
  color: white;
}

.header_btn_box {
  display: flex;
  /* align-items: center; */
  gap: 40px;
}

/* media */

@media screen and (max-width: 1400px) {
  .section_header_home {
    padding: 20px 10px;
  }
}

@media screen and (max-width: 970px) {
  .header_ul_li {
    display: none;
  }
  .header_open_menu {
    display: block;
  }
}

@media screen and (max-width: 490px) {
  .wrapper_header ul {
    gap: 10px !important;
  }
  .header_btn_box {
    gap: 10px !important;
  }
  .wrapper_header ul li .header_btn {
    padding: 12px 16px;
  }
}

@media screen and (max-width: 380px) {
  .header_btn_box {
    display: flex;
    flex-direction: column;

  }
}
