.wrapper_stages_media {
  display: flex;
  flex-direction: column;
  gap: 155px;
  align-items: center;
  justify-content: center;
  display: none;
}

.media_stage {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 24px;
  text-align: center !important;
}

.media_stage h3 {
  color: var(--2, #1b1821);
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  padding: 0;
  margin: 0 0 5px 0;
}

.media_stage p {
  color: var(--2, #1b1821);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  margin: 0;
  padding: 0;
}

/* media */

@media screen and (max-width: 885px) {
  .wrapper_stages_media {
    display: flex;
  }
}
