.section_home_hero {
  background: url(../../../assets/home/home-heroback.png) no-repeat;
  background-size: cover;
  padding: 64px 0px;
}

.section_home_hero .wrapper_home_hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.hero_bottom_back {
  height: 250px;
  clip-path: polygon(70% 0, 100% 0, 100% 100%);
  background-color: var(--2, #1b1821);
}

.wrapper_home_hero .hero_request_box {
  border-radius: 4px;
  background: #fff;
  box-shadow: 11px 13px 28px 0px rgba(0, 0, 0, 0.3);
  display: flex;
  width: 30%;
  padding: 40px;
  flex-direction: column;
  gap: 16px;
}

.hero_request_box h3 {
  margin: 0;
  padding: 0;
}

.hero_request_box .request_head_text {
  color: var(--2, #1b1821);
  font-size: 30px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  text-align: center;
}

.hero_request_box select {
  border-radius: 8px;
  border: 2px solid #aaa;
  padding: 18px 24px;
  color: var(--2, #1b1821);
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.hero_request_box input {
  color: var(--2, #1b1821);
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  padding: 18px 24px;
  border-radius: 8px;
  outline: none;
  border: 2px solid #aaa;
}

.hero_request_box button {
  border-radius: 4px;
  background: var(--1, #22adaa);
  box-shadow: 11px 13px 28px 0px rgba(0, 0, 0, 0.3);
  color: #fff;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  padding: 16px 24px;
  border: 1px solid #22adaa;
  cursor: pointer;
}

/* media */

@media screen and (max-width: 1400px) {
  .section_home_hero {
    padding: 64px 10px;
  }
}

@media screen and (max-width: 970px) {
  .hero_bottom_back {
    clip-path: polygon(50% 0, 100% 0, 100% 100%);
  }
}

@media screen and (max-width: 910px) {
  .wrapper_home_hero .hero_request_box {
    width: 40%;
  }
  .hero_bottom_back {
    clip-path: polygon(30% 0, 100% 0, 100% 100%);
    height: 200px;
  }
}

@media screen and (max-width: 700px) {
  .wrapper_home_hero {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }
  .wrapper_home_hero .hero_request_box {
    width: 60%;
  }
}

@media screen and (max-width: 470px) {
  .wrapper_home_hero .hero_request_box {
    width: 76%;
  }
  .hero_bottom_back {
    clip-path: polygon(10% 0, 100% 0, 100% 100%);
    height: 100px;
  }
}
