.section_footer {
  background-image: url(../../assets/home/home-footer.png);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 118px 0px 50px;
}

.wrapper_footer {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.head_footer {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.head_footer .footer_icons {
  display: flex;
  align-items: center;
  gap: 20px;
}

.footer_icons i {
  font-size: 30px;
  color: white;
}

.section_footer .footer_info {
  display: flex;
  align-items: center;
  gap: 300px;
}

.footer_info a {
  color: #fff;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.footer_info ul {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.footer_info ul:nth-child(2) li {
  display: flex;
  align-items: center;
  gap: 10px;
}

.footer_info p {
  color: #fff;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  padding: 0;
  margin: 0;
}

.footer_phonenumber_box {
  display: flex;
  align-items: center;
  gap: 8px;
}

.footer_phonenumber_box ion-icon {
  width: 30px;
  height: 30px;
  color: white;
}

.footer_phonenumber_box .numbers {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

/* medai */

@media screen and (max-width: 1400px) {
  .section_footer {
    padding: 118px 10px 50px;
  }
}

@media screen and (max-width: 580px) {
  .footer_info {
    display: flex;
    flex-direction: column;
    align-items: flex-start !important;
    justify-content: flex-start !important;
    gap: 50px !important;
  }
  .head_footer {
    display: flex;
    flex-direction: column;
    align-items: flex-start !important;
    justify-content: flex-start !important;
    gap: 30px;
  }
}
