.wrapper_business {
  padding: 64px 0px;
}

.wrapper_business h2 {
  color: var(--2, #1b1821);
  font-size: 30px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.55px;
}

.wrapper_business h3 {
  font-size: 184px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%;
  letter-spacing: 0.55px;
  margin: 0;
  padding: 0;
  color: #22adaa;
}

.bastet_business {
  display: flex;
  align-items: center;
  gap: 60px;
}

.car_business {
  display: flex;
  align-items: center;
  gap: 60px;
}

.bastet_business div p:nth-child(1) {
  color: var(--2, #1b1821);
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
}

.bastet_business div p:nth-child(2) {
  color: var(--2, #1b1821);
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 140%;
}

.car_business button {
  padding: 16px 104px;
  color: #fff;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  border-radius: 4px;
  background: var(--1, #22adaa);
  box-shadow: 11px 13px 28px 0px rgba(0, 0, 0, 0.3);
  border: none;
}

.car_business div p {
  color: var(--2, #1b1821);
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
}

/* media */

@media screen and (max-width: 1400px) {
  .wrapper_business {
    padding: 64px 10px;
  }
}

@media screen and (max-width: 885px) {
  .wrapper_business .bastet_business {
    display: flex !important;
    flex-direction: column-reverse !important;
  }
  .wrapper_business .car_business {
    display: flex;
    flex-direction: column;
  }
}

@media screen and (max-width: 575px) {
  .wrapper_business h3 {
    font-size: 104px;
  }
}
