.wrapper_home_stages {
  padding: 64px 0px;
  display: flex;
  flex-direction: column;
  gap: 64px;
}

.wrapper_home_stages h2 {
  color: var(--2, #1b1821);
  font-size: 30px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  text-align: center;
}

.home_stages {
  display: flex;
  justify-content: space-between;
}

.wrapper_home_stages .stage {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 40px;
}

.left_box_stages {
  padding: 0px 110px;
  border-right: 1px solid var(--2, #1b1821);
  display: flex;
  flex-direction: column;
  gap: 155px;
}

.right_box_stages {
  padding: 200px 110px 0px;
  display: flex;
  flex-direction: column;
  gap: 155px;
}

.head_stages h3 {
  color: var(--2, #1b1821);
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  padding: 0;
  margin: 23px 0 0 0;
}

.head_stages p {
  color: var(--2, #1b1821);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  margin: 0;
  padding: 0;
}

/* media */
@media screen and (max-width: 1400px) {
  .wrapper_home_stages {
    padding: 64px 10px;
  }
}

@media screen and (max-width: 1140px) {
  .left_box_stages {
    padding: 0px 40px;
    /* width: 70%; */
  }
  .right_box_stages {
    padding: 200px 40px 0px;
  }
}

@media screen and (max-width: 885px) {
  .home_stages {
    display: none;
  }
}
