.section_get_started {
  margin-top: 200px;
  padding: 310px 0px 210px;
  background-image: url(../../../assets/home/home-get-started-back.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.section_get_started .wrapper_get_started {
  display: flex;
  flex-direction: column;
  gap: 123px;
}
.wrapper_get_started .head_get_started {
  width: 70%;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.wrapper_get_started .head_get_started h3 {
  margin: 0;
  padding: 0;
  color: #fff;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.wrapper_get_started .head_get_started p {
  margin: 0;
  padding: 0;
  color: #fff;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.wrapper_get_started .home_contact {
  display: flex;
  align-items: center;
  justify-content: space-between !important;
}

.home_contact img {
  max-width: 600px;
  width: 60%;
}

.home_contact form {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.home_contact .contact_us_box {
  width: 30%;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.contact_us_box h3 {
  color: #fff;
  text-align: end;
  font-size: 30px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.55px;
  margin: 0;
  padding: 0;
}

.contact_us_box p {
  margin: 0;
  padding: 0;
  color: #fff;
  text-align: center;
  font-size: 20px;
  font-style: normal;
  font-weight: 300;
  line-height: 140%;
}

.contact_us_box input {
  padding: 16px 24px;
  border-radius: 4px;
  border: 2px solid #fff;
  background: rgba(255, 255, 255, 0);
  color: white;
}

.contact_us_box input::placeholder {
  color: white;
}

.contact_us_box textarea {
  resize: none;
  height: 180px;
  padding: 16px 24px;
  border-radius: 4px;
  border: 2px solid #fff;
  background: rgba(255, 255, 255, 0);
  color: white;
}

.contact_us_box textarea::placeholder {
  color: white;
}

.contact_us_box button {
  border-radius: 4px;
  background: #060606;
  box-shadow: 11px 13px 28px 0px rgba(0, 0, 0, 0.3);
  padding: 16px 0px;
  color: #fff;
  border: none;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

@media screen and (max-width: 1400px) {
  .section_get_started {
    padding: 310px 10px 210px;
  }
}

@media screen and (max-width: 875px) {
  .wrapper_get_started .head_get_started {
    width: 100% !important;
  }
  .home_contact {
    display: flex;
    flex-direction: column-reverse;
    gap: 50px;
  }
  .wrapper_get_started {
    gap: 50px !important;
  }
  .contact_us_box {
    width: 80% !important;
  }
  .home_contact h3 {
    text-align: center !important;
  }
}

@media screen and (max-width: 420px) {
  .contact_us_box textarea {
    height: 100px;
  }
  .section_get_started {
    padding: 110px 10px 210px;
  }
}
