.wrapper_hom_weekly h2 {
  text-align: center;
  color: var(--2, #1b1821);
  margin-bottom: 50px;
  font-size: 30px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.home_weekly_card {
  border-radius: 4px;
  border: 1px solid #f1f1f1;
  background: #fff;
  box-shadow: 11px 13px 28px 0px rgba(0, 0, 0, 0.2);
  padding: 30px 20px;
  overflow: hidden;
  background-image: url(../../../assets/home/home-card.png);
  background-repeat: no-repeat;
  background-size: 100%;
  display: flex;
  flex-direction: column;
  gap: 61px;
  margin-bottom: 30px;
}

.home_weekly_card h3 {
  margin: 0;
  padding: 0;
}

.home_weekly_card p {
  margin: 0;
  padding: 0;
}

.home_weekly_card .card_head_text h3 {
  color: #fff;
  text-align: center;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.55px;
}

.home_weekly_card .card_head_text p {
  color: #e9e9e9;
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.55px;
}

.home_weekly_card .card_lists {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.home_weekly_card .card_price_text {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  border-bottom: 1px solid #cdcdcd;
  padding-bottom: 14px;
}

.home_weekly_card .card_price_text p {
  color: var(--2, #1b1821);
  text-align: center;
  font-size: 36px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 0.55px;
}

.home_weekly_card .card_price_text span {
  color: var(--2, #1b1821);
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.55px;
  margin: 0;
  padding: 0;
}

.home_weekly_card ul {
  list-style: disc;
  margin-left: 40px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.home_weekly_card button {
  color: #fff;
  padding: 16px 0px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  border-radius: 4px;
  background: var(--1, #22adaa);
  box-shadow: 11px 13px 28px 0px rgba(0, 0, 0, 0.3);
  border: none;
}
