.header_hamburger_wrapper ul {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.header_hamburger_wrapper ul li {
  border-bottom: 1px solid gray;
  padding-bottom: 14px;
}

.header_hamburger_wrapper ul li a {
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  color: black;
  cursor: pointer;
}

.header_hamburger_wrapper .header_phonenumber_box {
  display: flex;
  align-items: center;
  gap: 8px;
}

.header_phonenumber_box ion-icon {
  width: 30px;
  height: 30px;
  color: black;
}

.header_hamburger_wrapper .numbers {
  display: flex;
  flex-direction: column;
  gap: 4px;
}