.hero_accordion {
  width: 385px;
}

.hero_accordion .ant-collapse-item {
  border: none !important;
}

.hero_accordion .ant-collapse {
  border: none !important;
}

.hero_accordion .ant-collapse-header {
  padding: 0 !important;
  align-items: center !important;
}

.hero_accordion .ant-collapse-expand-icon {
  padding: 0 !important;
  margin: 0 !important;
  border-radius: 50% !important;
}

.hero_accordion .ant-collapse-content {
  padding: 0px 6px !important;
  background-color: transparent !important;
}

.ant-collapse-content-active {
  padding: 0 !important;
  margin: 0 !important;
  border-top: none !important;
}

.hero_accordion .ant-collapse-content-box {
  padding: 0 !important;
  margin: 0 !important;
  border-left: 1px solid transparent;
}
.hero_accordion .ant-collapse-content-active {
  position: relative;
}

.hero_accordion .ant-collapse-content-active::before {
  content: "";
  width: 0.5px;
  height: 105%;
  background-color: white;
  position: absolute;
  bottom: 0;
  top: -2px;
  left: 2px;
}

.hero_accordion p {
  margin: 0;
  padding: 16px;
  color: #fff;
  font-size: 22px;
  font-style: normal;
  font-weight: 600;
  line-height: 140%;
}

.hero_accordion .ant-collapse-item-active .ant-collapse-header-text {
  overflow: hidden;
  height: 0px;
}

.hero_accordion .ant-collapse-expand-icon {
  height: auto !important;
}

.hero_accordion span {
  color: #fff !important;
  font-size: 16px !important;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  padding: 0px 16px 0px;
}

.hero_accordion button {
  color: var(--2, #1b1821);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  padding: 16px 74px;
  line-height: normal;
  border-radius: 4px;
  background: #fff;
  box-shadow: 11px 13px 28px 0px rgba(0, 0, 0, 0.3);
  border: none;
  margin-top: 24px;
}

/* media */

@media screen and (max-width: 700px) {
  .hero_accordion {
    width: 70%;
  }
}

@media screen and (max-width: 470px) {
  .hero_accordion {
    width: 95%;
  }
}
